.MainMenu {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #14240b;
  padding: 20px;
}

.talents {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-content: center;
}

.talent {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;

  /* Медиа-запросы для уменьшения размера изображения при уменьшении экрана */
  @media (max-width: 600px) {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}

.talent:hover {
  transform: scale(1.05);
}

.talent img {
  max-width: 100%;
  max-height: 200px; /* Ограничение на высоту изображения */
  object-fit: cover; /* Корректное отображение изображения */
  border-radius: 10px;
}

.talent h2 {
  margin: 10px 0;
  font-size: 1.5em;
  color: #333;
}

.talent button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.talent button:hover {
  background-color: #0056b3;
}

.additional-images {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.additional-images img {
  max-width: 100%;
  max-height: 200px; /* Ограничение на высоту изображения */
  object-fit: cover; /* Корректное отображение изображения */
  border-radius: 10px;
}
